.splash-screen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    background: white;
    z-index: 999999;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: 30px;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
    font-family: 'Helvetica';
}

.splash-screen-close-button {
    position: absolute;
    top: 0;
    right: 0;
    padding: 20px;
}


