.carousel-container {
  position: relative;
  height: 130px;
  /* max-width: 960px; */
  margin: 0 auto;
  overflow: hidden;
  -ms-touch-action: pan-y;
      touch-action: pan-y;
}

.carousel-track {
  margin-left: 10px;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}

.carousel-card {
  padding: 10px;
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
  -webkit-tap-highlight-color: transparent;
}

.carousel-card-inner {
  -ms-flex-direction: column;
      flex-direction: column;
  width: 125px;
  height: 120px;
  /* border-radius: 5px; */
}

.carousel-image-container {
  width: 125px;
  height: 75px;
  background: #f5f5f5;
}

.carousel-image {
  width: 125px;
  height: 75px;
}

.carousel-title {
  font-family: 'EngraversGothicBold';
  font-weight: bold;
  font-size: 14px;
  line-height: 13px;
  letter-spacing: 1.8px;
  padding-top: 5px;
  overflow: hidden; 
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  text-transform: uppercase;
}

.carousel-subtitle {
  font-size: 14px;
  color: rgba(87, 87, 87, 0.6);
  overflow: hidden; 
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}



