.city-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 99999;
  background: white;
}

.city-ios-scroll-fix {
  transform: translate3d(0,0,0);
  -webkit-transform: translate3d(0,0,0);
}


.city-header-image {
  text-align: center;
  color: #ffffff;
  padding: 39px 0;
}

.city-header-image h2, .city-header-image p {
  margin: 0;
  font-family: 'EngraversGothicBold';
}

.city-header-image p {
  font-size: 16px;
  letter-spacing: 2px;
}

.city-header-image h2 {
  font-size: 38px;
  letter-spacing: 5px;
  margin-top: 9px;
}

.city-copy {
  padding: 20px;
  border-bottom: 1px solid #efefef;
}

.city-copy p {
  margin: 0;
  color: #575757;
}

.city-content-title {
  font-family: 'EngraversGothicBT';
  font-size: 14px;
  color: #000000;
  letter-spacing: 1.8px;
  padding: 0 20px;
  margin-top: 20px;
  margin-bottom: 0px;
}

.city-experience-container {
  display: -ms-flexbox;
  display: flex;
  padding: 15px 20px;
  text-decoration: none;
}

.experience-image-container {
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
  top: 0;
  left: 0;
}

.experience-text-container {
  -ms-flex: 6 1;
      flex: 6 1;
}

.experience-logo {
  width: 40px;
  border-radius: 50%;
}

.experience-title {
  font-size: 14px;
  font-weight: bold;
  line-height: 0.93;
  letter-spacing: 1.8px;
  font-family: 'EngraversGothicBold'
}

.experience-type {
  opacity: 0.6;
  text-transform: capitalize;
	font-family: 'SFDisplay';
	font-size: 14px;
	text-align: left;
	color: #575757;
}

.experience-desc {
  font-family: SFProDisplay;
	font-size: 15px;
	line-height: 1.33;
	text-align: left;
	color: #575757;
}

.experience-type-icon {
  width: 9px;
}

.experience-icon-container {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  position: absolute;
  top: -3px;
  left: -3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


